@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');
a{
  color: black !important;
}

.brand-logo,h2{
  font-family: 'Grand Hotel', cursive;
}
.mycard{
  margin-top: 30px
}
.auth-card{

  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 10px auto;
}
.input-field input[type=text]:focus {
  border-bottom: 1px solid rgb(8,93,252) !important;
  box-shadow: 0 1px 0 0  rgb(8,93,252) !important;
}
.input-field input[type=email]:focus {
  border-bottom: 1px solid rgb(8,93,252) !important;
  box-shadow: 0 1px 0 0  rgb(8,93,252) !important;
}
.input-field input[type=password]:focus {
  border-bottom: 1px solid rgb(8,93,252) !important;
  box-shadow: 0 1px 0 0  rgb(8,93,252) !important;
}
.gallery{

  display:flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.item{
  width: 30%;
}
.home-card{
  max-width: 500px;
  height:max-content;
  margin:26px auto;
}
#toast-container{
  top: 30px !important;
  right: 20px !important;
  left:auto !important;
}
.material-icons:hover{

  cursor: pointer;
}
.postlogo {
  width: 30px; /* Adjust the width as needed */
  height: 30px; /* Adjust the height as needed */
  border-radius: 50%; /* To make the image circular */
  margin-right: 10px; /* Add some spacing between the logo and the name */
  object-fit: cover; /* Ensure the image fits within the container */
  vertical-align: middle; /* Align the image vertically within the line */
}